.section-consultancy{
    position: relative;
    z-index: $zi-top-ui-bg;
} 

.consultancy-content {
    position: absolute;
    top: 30%;
    left: 10%;

    &__secondary-heading {
        max-width: 40rem;
    }

    .highlight-headline-box{
        margin: 0;
        margin-bottom: 2rem;
    }

    &__paragraph {
        font-size: $body-small-ft;
        width: 40rem;
        margin-left: 4rem;
    }
}

.consultancy__go-to {
    position: absolute;
    top: 80%;
    left: 70%;
}

.consultancy-background{
    background: linear-gradient(to bottom right, transparent 0%, transparent 60%, $secondary-bright-2 60%, $secondary-bright-2 100%);
    svg {
        display: block;
        border-bottom: 5px solid $primary-color;
        width: 100%;
        max-height: 95vh;
        path {
            fill: none;
            stroke-width: 1.5px;
        }
        #branch-1-path-1{
            stroke: $secondary-rest-3;
        }
        #branch-1-path-2{
            stroke: $secondary-rest-2;
        }
        #branch-1-path-3{
            stroke: $secondary-rest-1;
        }
        #branch-1-path-4{
            stroke: $secondary-rest-2;
        }
        #branch-1-path-5{
            stroke: $secondary-rest-1;
        }
        #branch-2-path-1{
            stroke: $secondary-rest-3;
        }
        #branch-2-path-2{
            stroke: $secondary-rest-3;
        }
    }
}
