.modal {
    display: block;
    border-radius: 3px;

    width: 70%;
    height: 85%;

    position: fixed;

    z-index: $zi-absolute-top;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);    
    background: white;
    box-shadow: 0 0 60px 10px rgba(0, 0, 0, 0.9);

    &-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: $zi-overlay;
        background: rgba(0, 0, 0, 0.6);
    }

    &__content {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        overflow: auto;
        margin: 6rem 5rem;
        &--aside {
            padding-right: 3rem;
            border-right: 1px solid #ccc;
            flex: 1;
            
            .modal-email {
                font-size: $small-ft;
            }
        }
    
        &--text{
            flex: 2;
            padding-left: 2rem;
            margin: 1rem auto;
        }
    }

    &__image-container  {
        img {
            height: 100%;
            width: 100%;
            border-radius: 3px;
        }
        &--1{
            img {
                background-color: $secondary-accent-1;
            }
        }
        &--2{
            img {
                background-color: $primary-color;
            }
        }
        &--3{
            img {
                background-color: $secondary-dark;
            }
        }
        &--4{
            img {
                background-color: $secondary-accent-2;
            }
        }
    }

    &__close-button {
        position: absolute;
        z-index: $zi-base;
        top: 1rem;
        right: 2rem;
        width: 5rem;
        opacity: .5;
        &:hover {
            cursor: pointer;
        }
    }

    &__linked-in-logo{
        svg {
            position: relative;
            display: block;
            width: 5rem;
            fill: #ccc;
            // &:hover{
            //     fill: $secondary-accent-1;
            // }
        }
        &--1{
            svg {
                &:hover{
                    fill: $secondary-accent-1;
                }
            }
        }
        &--2{
            svg {
                &:hover{
                    fill: $primary-color;
                }
            }
        }
        &--3{
            svg {
                &:hover{
                    fill: $secondary-dark;
                }
            }
        }
        &--4{
            svg {
                &:hover{
                    fill: $secondary-accent-2;
                }
            }
        }
    }



}

.closed {
    display: none;
}

  
  
  

  

