.team-member-box {
    position: relative;
    justify-self: center;
    width: 70%;
    margin-bottom: 5rem;
    height: 50rem;
    z-index: $zi-base;
    
    &__image-content{
        position: relative;
        height: 35rem;
        width: 30rem;
        img {
            // position: absolute;
            float: right;
            padding: 4rem 0 0 3rem;
            bottom: 12rem;
            height: 100%;
        }
        @include clearfix;
        &:hover :nth-child(1){
            height: 100%;
            width: 75%;
        }
    }
    
    
    &__background{
        position: absolute;
        top: 0;
        left: 0;
        width: 60%;
        height: 70%;
        z-index: $zi-behind;
        transition: height .5s, width .5s;
        &--1{
            background-color: $secondary-accent-1;
        }
        &--2{
            background-color: $primary-color;
        }
        &--3{
            background-color: $secondary-dark;
        }
        &--4{
            background-color: $secondary-accent-2;
        }
    }
    


    &:hover{
        cursor: pointer;
    }

    &__description {
        h2 {
            font-size: $headline-small-ft;
        }
        h3 {
            font-size: $body-ft;
        }
    }

}