.dsp-grid {
    display: grid;
}

.center {
    margin: 0 auto;
}

.flex {
    display: flex;
}

.flex-space-evenly {
    justify-content: space-evenly;
}

.flex-end {
    justify-content: flex-end;
}

.hide-on-small {
    @include breakpoint-down(medium){
        display: none;
    }
}

.hide-on-medium {
    @include breakpoint-up(large){
        display: none !important;
    }
}

.completely-hidden {
    display: none;
}