.main_plantbased {
    margin-top: 2rem;
    background: linear-gradient(to right, $secondary-bright-2 0%, $secondary-bright-2 65%, $secondary-bright 65%, $secondary-bright 100%);

}


.section-products {
    margin: 0 $sidebar-gutter 5rem 0;
    // http://www.independent-software.com/set-stacking-order-of-pseudo-elements-below-parent-element.html
    position: relative;
    z-index: $zi-base;
}

.product-container {
    margin-top: 18rem;

    &--float-right {
        float: right;
    }
    &--floated {
        @include clearfix;
    }


}

// TODO: only if connection anim
.full-page-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
}

// TODO: only if connection anim
.box-connecting-path {
    fill: none;
    stroke-width: .5rem;
  }

