* {
    margin: 0;
    padding: 0;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  html {
    font-size: 62.5%; // 1rem=10px, 10/16=62.5%
  }
  
  body {
    box-sizing: border-box;
  }

  ::selection{
    background-color: $secondary-accent-1;
    color: $secondary-bright;
  }