.nav {
    $nav-padding: 3rem 4rem;
    $nav-margin: 0 0rem;
    position: relative;
    z-index: $zi-top-ui;
    &__content, &__content--footer {
        float: right;
        color: $contrast-dark;
        list-style-type: none;
        border-radius: 3px;
        font-size: $small-ft;
        
        &:last-child {
            margin-right: 2rem;
        }
        @include clearfix;
    }
    
    &__link {
        cursor: pointer;
        margin: $nav-margin;
        padding: $nav-padding;
        display: inline-block;
        text-decoration: none;
        &:link,
        &:visited {
            color: $contrast-dark;
        }
        &:active {
    
        }
    }
    

    &__second-lev-ul, &__second-lev-ul--footer {
        display: none;
        &:last-child{
            border-radius: 0 0 8px 8px;    
        }
    }

    &__dropdown::after{
        content: "\232A";
        display: inline-block;
        transform: translateX(-3.5rem) rotate(90deg) translateX(.5rem);
        transition: transform 300ms ease-in-out;
    }

    &__first-lev, &__first-lev--footer {
        display: inline-block;
        position: relative;
        &:hover {
            &::after{
                transform: translateX(-3.5rem) rotate(-90deg) translateX(.5rem);
            }

            // cursor: pointer;
            @include navunderline;
            .nav__second-lev-ul {
                position: absolute;
                display: block;
                width: 170%;
                left: 0;
                background-color: rgba($secondary-bright, .8);
            }
        }
    }

    &__second-lev {
        .nav__link{
            padding-top: 1.5rem;
            padding-bottom: 1.5rem;
        }
        display: block;  
        :hover {
            color: $secondary-accent-1;
        }                  
    }

    &__logo {
        display: inline-block;
        svg{
            height: 8rem;
            .cls-1{fill:#fff;}
            .cls-2{fill:#323e48;}
            .cls-3{fill:#a2d45e;}
        }
    }

    
    &--hamburger{
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 100vh;
    }
    
    &--hamburger--center-container{
        position: relative;
    }

    &--hamburger--center{
        position: fixed;
        height: 100vh;
        min-width: 25rem;
        right: -25rem;
        z-index: $zi-absolute-top-bg;
        background-color: rgb(42, 41, 41);
        transition: transform 500ms ease-in-out;
        &.open {
            transform: translate(-25rem, 0);
        }
    }
    
    &__content--hamburger{
        margin: auto;
        // TODO: depending on design, have other transition and location centered...
        // text-align: center;
        ul {
            list-style-type: none;
        }
        .nav__second-lev--hamburger {
            padding-left: 1rem;
        }
        // position: absolute;
        // top: 0;
        // left: 50%;
    }

    &--hamburger__hamburger-icon {
        position: absolute;
        z-index: $zi-absolute-top;
        top: 2rem;
        right: 2rem;
        fill: $contrast-dark;
        :hover{
            cursor: pointer;
        }

        svg {
            width: 3rem;
            height: 3rem;
            .open {
                // TODO: animate to close
                // > rect:first-child{
                //     transform-origin: 0.2rem 0;
                //     transform: rotate(45deg);
                // }
                
                // > rect:nth-child(2){
                //     opacity: 0;
                // }
                
                // > rect:last-child {
                //     transform-origin: 5 6rem;
                //     transform: rotate(-45deg);
                // }
            }
        }
    }
    
}