.page-sidebar {
    position: fixed;
    z-index: $zi-mid;
    // Weird Chrome 65 bug 
    // https://axe369.medium.com/chrome-position-sticky-with-z-index-problem-4b57bb80816e
    transform: translateZ(0);
    top: 0;
    left: 0;
    background-color: $secondary-accent-2;
    width: $sidebar-gutter;
    height: 100vh;
    flex-direction: column;
    justify-content: center;

    &__content{
        color: $contrast-dark;
        text-align: center;
        white-space: nowrap;
        text-transform: uppercase;
        font-size: $small-ft;
        letter-spacing: .4rem;
        transform: rotate(90deg) translateX(-50%);
    }
}