.contact-section{

    &__background {
        background: linear-gradient(to bottom right, #fff 0%, #fff 50%, $secondary-accent-2 50%, $secondary-accent-2 100%);
        margin-top: 4rem;
        padding-bottom: 3rem;       
    }
    .highlight-headline-box{
        margin: 3rem 5rem;
    }
    
    .form-container {
        width: 60%;
        margin:auto;
        padding: 3rem 3rem;
        // margin-bottom: 3rem;
        background-color: $secondary-bright-2;
        border: 1px solid $contrast-dark;
        border-radius: 3px;
        font-size: $body-ft;
    }
}
