.header{
    position: relative;
    background-color: $secondary-bright;
    z-index: $zi-top-ui;
    // Chrome 65 bug
    transform: translateZ(0);
    border-bottom: 3px solid $primary-color;
}

.header__hero {
    height: 100vh;
    background-image: linear-gradient(to right bottom, rgba($secondary-dark, .8), rgba($secondary-rest-1, 0.4) 60%), url("../../assets/img/noah-buscher-x8ZStukS2PM-unsplash.jpg");
    background-size: cover;
    position: relative;
    border-bottom: solid .5rem $primary-color;
}

.header__background {
    position: relative;
    z-index: $zi-top-ui-bg;
    background-color: $secondary-bright;
    border-bottom: 3px solid $primary-color;
}



