.footer {
    position: relative;
    background-color: $secondary-bright;
    color: $contrast-dark;
    padding-bottom: 1rem;
    border-top: 1px solid $primary-color;

    &__bg {
        svg {
            height: 100%;
            circle {
                fill: $secondary-accent-1;
            }
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
        }
    }

    &__content{
        padding: 3rem 2rem 3rem 5rem;
    }

    &__company{
        flex: 1;
    }
    &__info{
        flex: 2;
        border-left: 1px solid grey;
        padding-left: 2rem;
    }
    &__copyright{
        text-align: center;
        margin: auto;
        font-size: $very-small-ft;
    }
    &__logo{
        height: 12rem;
        svg {
            .cls-1{fill:#fff;}
            .cls-2{fill:#323e48;}
            .cls-3{fill:#a2d45e;}
        }
    }
}