main{
    margin-left: $sidebar-gutter + 2rem;
}

$section-top-margin: 10rem;

.section-cons-fields{
    margin-top: $section-top-margin;
    .highlight-headline-box{
        margin-bottom: 3rem;
    }
    &__list{
        line-height: 4rem;
        padding-left: 2rem;
        border-left: 2px solid $primary-color;
        ul{
            list-style-type: none;
        }
    }
    
    &__img-container{
        position: relative;
        @include offsetborder;
        &::after {
            border: solid 2px $primary-color;
        }
        @include offsetbackground;
        &::before {
            background-color: $primary-color;
        }
        
        height: 40rem;
        img{
            height: 100%;
        }
    }
}

.section-cons-process{
    // TEMP - should have more coherent background over sections
    background-color: $secondary-bright-2;
    z-index: $zi-behind;
    margin-top: $section-top-margin;
    padding: $section-top-margin 0;
    .headline-right{
        margin-right: 30rem;
        margin-bottom: 8rem;
    }

    &__process-steps{
        margin-top: 4rem;
    }
    &__progress-card-parent{
        margin: 0 2rem;
        width: 20%;
        position: relative;
        background-color: $secondary-bright;
        transform: scale(1);
        transition: transform 200ms ease-in-out;
        &:hover{
            transform: scale(1.05);
        }
    }
    &__progress-card{
        padding: 0 2rem;
        .number-container{
            position: relative;
            // display: inline-block;
            text-align: center;
            margin: 0 auto;
            padding: 2rem 0;
            font-size: $headline-mid-ft;
            color: $secondary-dark;
            &::after{
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -52%);
                width: 5rem;
                height: 5rem;
                border: 3px solid $secondary-rest-1;
                border-radius: 50%;
            }
        }
        @include offsetborder;
        &::after {
            border: solid 2px $secondary-accent-1;
        }
        // @include offsetbackground;
        // &::before {
        //     background-color: $secondary-accent-1;
        // }
    }
}

.section-cons-framework {
    margin-top: $section-top-margin;
    .highlight-headline-box{
        margin-bottom: 3rem;
    }
    &__summary-container{
        display: block;
    }
    &__summary{
        margin: 0 auto;
        position: relative;
        background-color: $secondary-bright;
        padding: 2rem;
        width: 50%;
        @include offsetborder;
        &::after {
            border: solid 2px $secondary-accent-4;
        }
        @include offsetbackground;
        &::before {
            background-color: $secondary-accent-4;
        }
    }

    .framework-container {
        position: relative;
    }
    .framework-bg{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        svg {
            width: 100%;
            height: 100%;
            path {
                fill: none;
                stroke: $secondary-accent-1;
                stroke-width: .4px;
                stroke-linecap: round;
            }
        }
    }

    .dsp-grid-framework{
        margin-top: 5rem;
        grid-template-columns: repeat(5, 1fr [col-start]);
        grid-template-rows: repeat(5, 1fr);
        
        [class^="item"]{    
            position: relative;
            // margin: 3rem auto;
            margin: 0 2rem;

            svg {
                display: inline-block;
                height: 6rem;
                path {
                    stroke: $contrast-dark;
                    fill: $contrast-dark;
                }
                .path-highlight{
                    fill: $secondary-accent-1;
                    stroke: $secondary-accent-1;
                }
            }
        }

        .framework-item__text-container{
            padding-bottom: 1rem;
        }
        
        [class*="framework-item-container"]{
            transition: transform 800ms ease-in-out;
            transform: scale(1);
            &.active {
                transform: scale(1.1);
                .cross-hair{
                    transform-origin: center;
                    animation: animate-aim 800ms ease-in-out forwards;
                }
                #ruler-tick-1{
                    animation: animate-ruler-tick-1 1200ms ease-in-out forwards;
                }
                #ruler-tick-2{
                    animation: animate-ruler-tick-2 1200ms ease-in-out forwards;
                }
                #ruler-tick-3{
                    animation: animate-ruler-tick-3 1200ms ease-in-out forwards;
                }
                .magnifier-content{
                    animation: animate-analytics 1200ms ease-in-out forwards;
                }
                .rocket-fuel{
                    transform-origin: 36% 28%;
                    animation: animate-rocket 1200ms ease-in-out forwards;
                }
                .move-controller{
                    animation: animate-controller 1200ms ease-in-out forwards;
                }

            }
            background-color: $secondary-bright;
            text-align: center;
            // @include offsetborder;
            // &::after {
                border: solid 2px $secondary-accent-4;
            // } 
            // @include offsetbackground;
            // &::before {
            //     background-color: $secondary-accent-4;
            // }


        }
        .item-2 {
            grid-column: 2;
            grid-row: 2;
        }
        .item-3 {
            grid-column: 3;
            grid-row: 3;
        }
        .item-4 {
            grid-column: 4;
            grid-row: 4;
        }
        .item-5 {
            grid-column: 5;
            grid-row: 5;
        }
    }
}

.section-cons-more-info {
    position: relative;
    $arrow-width: 14rem;
    $arrow-start-left: 10rem;
    .arrow-container{
        display: inline-block;
        position: absolute;
        top: -16rem;
        left: $arrow-start-left;
        svg {
            width: $arrow-width;
            path {
                fill: none;
                stroke: $secondary-accent-2;
                stroke-width: 1px;
                stroke-linecap: round;
            }
        }
    }
    .highlight-headline-box{
        margin-left: $arrow-start-left + $arrow-width + 2rem;
    }
    margin-top: $section-top-margin;
    padding-top: 5rem;
    background-color: $secondary-bright-2;
    &__goto{
        text-align: baseline;
        margin-top: 2rem;
        text-align: center;
        padding: 2rem 0;
    }
}